.view-container{
    margin: 16px;
    padding: 15px;
    background-color: #464646;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.tool-bar{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    background-color: #292929;
    width: 100%;
    /* margin: auto; */
    border-radius: 10PX;
    padding: 10px;
}

.tool-bar .right,
.tool-bar .search-conatiner {
    display: flex;
    flex-direction: row;
    align-items: center;
    overflow: auto;
}