.table-container{
    overflow: auto;
    max-height: 80vh;
}

.table-container .no-data-found{
    padding: 20px;
    text-align: center;
}

.table-container .load-more-container{
    margin: auto;
    text-align: center;
    padding: 20px;
}

.data-row th a{
    text-decoration: underline;
    cursor: pointer;
    color: #7b89ff;
}

.table-container table thead th:last-child,
.table-container table tbody td:last-child{
    display: none;
}

.table-container.allow-editing table thead th:last-child,
.table-container.allow-editing table tbody td:last-child{
    display: table-cell;
}

.data-row a{
    text-decoration: underline;
    cursor: pointer;
    color: #7b89ff;
}